      /**
       * 发货请求记录i
       * 查看发货请求记录所用
       */
      deliverySendRecord(index, row) {
        this.$router.replace({
          path: '/productionsend'
        });
        // productionsend
      },
      /**
       * 发货请求
       * @param index
       * @param detail
       */
      deliveryRequest(index, detail) {
        let that = this;
        this.dialogsendTitle = "发货请求";
        this.sendDetail.salePlanId = detail.id;
        this.CurrentSalesId = detail.id;
        if (detail.resendId) {
          this.getRequest("/shippingrequestdetails/findById?id=" + detail.resendId).then(resp => {
            if (resp && resp.status === 200) {
              var data = resp.data.root.shipdetail;
              that.sendDetail = {
                id: data.id,
                planNumber: data.planNumber,
                expectLevel: data.expectLevel,
                note: data.note,
                salePlanId: data.salePlanId,
              };
              that.dialogSendVisible = true;
            } else {
              that.$message({
                title: '查询失败'
              })
            }
          })
        } else {
          this.dialogSendVisible = true;
        }


      },
      cancelSenddetail() {
        this.clearSendDetail();
        this.dialogSendVisible = false;
      },
      //撤销发货请求
      revokeBeg() {
        let _this = this;

        this.getRequest("/shippingrequestdetails/revokebeg?id=" + this.sendDetail.id).then(resp => {
          if (resp && resp.status === 200 && resp.data.success) {
            _this.cancelSenddetail()
            _this.loadData();

          }
        })

      },
      clearSendDetail() {
        this.sendDetail = {
          id: 0,
          planNumber: '',//发货亲求数量
          note: '',//备注
          expectLevel: '',//请求等级
          salePlanId: '',
        }
      },
      //发货请求
      addSend(addForm) {
        console.log('fastsong');
        let _this = this;
        let detail = _.cloneDeep(this.sendDetail);
        console.log(detail);

        this.$refs[addForm].validate((valid) => {
          if (valid) {
            if (_this.sendDetail.id) {
              //更新
              this.tableLoading = true;
