      },
      doDelete(ids) {
        this.tableLoading = true;
        let _this = this;
        this.getRequest("/sales/delete?id=" + ids).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            let data = resp.data;
            _this.loadData();
          }
        })
      },
      clearDates() {
        this.startDate = '';
        this.endDate = '';
      },
      loadData() {
        let _this = this;
        this.tableLoading = true;
        this.getRequest("/sales/find?page="
          + this.currentPage
          + "&size=10&status=" + this.manageStatus
          + "&start=" + this.startDate
          + "&end=" + this.endDate
          + "&clientName=" + this.keyClientName
          + "&productName=" + this.keyproductName
          + "&empName=" + this.keyempName
        )
          .then(resp => {
            this.tableLoading = false;
            if (resp && resp.status === 200 && resp.data.success) {
              console.log('dfanfasf');
              _this.plansList = _this.preDataSale(resp.data.data);
              console.log(_this.plansList);
              _this.totalCount = resp.data.total;
              _this.emptyData();
            }
          })
      },
      emptyData() {
        // this.marketPlanDetails = [];
        // this.plan = {
        //   materialType: '',
        //   notes: '',
        //   serialNumber: '',
        //   clientId: 0,
        //   clientName: ''
        // };
        this.detail = {
          id: 0,
          clientId: [],
          productNo: '',
          notes: '',
          quantity: '',
          proId: '', //产品Id
          specsId: '',//规格Id
          colorId: '',
          shopId: '',
          shopchildId: '',
        };
        this.detailIDs = '';
      },
      back() {
        if (this.showDetaillist) {
          this.showDetaillist = false;
          this.showPlan = true;
        } else {
          this.showDetaillist = true;
          this.showPlan = false;
        }


      },
      //加载页面
      loadPlans() {
        let _this = this;
        this.tableLoading = true;
        this.getRequest("/productionplandetails/findbypage?page="
          + this.currentPage
          + "&size=10&status=" + this.manageStatus
          + "&productName=" + this.keyproductName
          + "&empName=" + this.keyempName
          + "&clientName=" + this.keyClientName
          + "&startDate=" + this.startDate
          + "&endDate=" + this.endDate
        )
          .then(
            resp => {
              _this.tableLoading = false;
              if (resp && resp.status === 200 && resp.data.success) {
                let list = resp.data.data || [];
                _this.productionPlans = list;//_this.preForData(list);//
                //没有当天的
                _this.totalCount = resp.data.total;
              } else {
                this.$message({type: 'infor', message: '查询失败'})
              }
            })
      },
      // 数据模式改变
      preDataSale(data) {
        let arr = [];
        if (data.length) {
          for (let i = 0, n = data.length; i < n; i++) {
            let e = data[i];
            let color = e[18] + '';
            let resendStr = this.string2Arr(e[22]);
            //下发记录显示
            //下发记录显示
            let planString = this.string2Arr(e[10]);
            let shippingStr = this.string2Arr(e[24]);
            let produtRecordStr = this.string2Arr(e[26]);
            console.log("发货请求" + resendStr)
            arr.push({
              id: e[0],
              state: e[1],
              clientName: e[2],
              productName: e[3],
              empName: e[4],
              specName: e[5],
              createDate: e[6],
              expecNum: e[7],
              completeNum: e[8],
              sendNum: e[9],
              planString: planString,
              note: e[11],
              colorName: e[12],
              parentName: e[13],
              clientIds: e[14],//父子关系Id保存
              productId: e[15],//产品
              colorId: e[16],//颜色
              specsId: e[17],//规格
              expectLevel: e[18],//重要程度
              shippingQuantity: e[19],//已发货到 数量
              resendNo: e[20],//已发货到 数量
              serialNumber: e[21],//编号
              resendStr: resendStr,//编号[22]
              editerId: e[23],//编号
              shippingStr: shippingStr,//编号
              resendId: e[25],//编号
              produtRecordStr: produtRecordStr,
              upsDate: e[27],
              checked: false,
              forOrder: true,
              toChange: false,//xian
            });

          }
        }
        return arr;
      },
      //将固定格式的字符串转换为数组；
      string2Arr(str) {
        return (str === null || str == undefined || str === '') ? [] : str.split(';');
      },

      toTree(data) {
        console.log('toTree');
        let that = this;
        let result = [];
        if (!Array.isArray(data)) {
          return result
        }
        var treeData = [];
        data.forEach(item => {
          treeData.push({
            'value': item.id,
            'label': item.name,
            'parentId': item.parentClientId
          })
        });
        let map = {};
        treeData.forEach(item => {
          map[item.value] = item;
        });
        treeData.forEach(item => {
          let parent = map[item.parentId];
          if (parent) {
            (parent.children || (parent.children = [])).push(item);
          } else {
            result.push(item);
          }
        });
        result = that.getResult(result)
        console.log(result);
        //将父parentId 删除掉
        return result;
      },


      getResult(reusult) {
        let that = this;
        reusult.map(item => {
          delete item.parentId;
          if (item.children && item.children.length) {
            that.getResult(item.children)
          }
        });
        return reusult;
      },
      showChanges() {
        this.plansList.map(item => item.toChange = !item.toChange)
      }
    },
    computed: {
      routes() {
        return this.$store.state.routes
      }
    },


  }

</script>
