      //查询生产计划(该生产计划未被归入合同中)
      loadSalse() {
        let _this = this;
        let clientid='';
        this.tableLoading = true;
        if (this.order.cliId && this.order.cliId.length) {
          clientid = this.order.cliId[this.order.cliId.length-1];
        }
        if (clientid==='') {
          Message("请选择客户@--！");
          return false;
        } else {
          this.getRequest("/productionplandetails/findForOrder?page="
            + this.currentItemPage
            + "&size=10&clientId=" + clientid
          ).then(resp => {
            this.tableLoading = false;
            if (resp && resp.status === 200 && resp.data.success) {
              _this.orderitems = resp.data.data;
              _this.totalItemCount = resp.data.total;
              _this.dialogUnSaleVisible = true;
            }else{
              Message('查询错误！')
            }
          })
        }
      },
      toTree(data) {
        let result = [];
        if (!Array.isArray(data)) {
          return result
        }
        data.forEach(item => {
          delete item.child;
        });
        let map = {};
        data.forEach(item => {
          map[item.id] = item;
        });
        data.forEach(item => {
          let parent = map[item.parentClientId];
          if (parent) {
            (parent.child || (parent.child = [])).push(item);
          } else {
            result.push(item);
          }
        });
        console.log(result);
        return result;
      }
    },


  }

