          receiver: '',//联系人
          relation: '',//联系人电话
          note: '',//备注e
        };
      },
      back() {
        this.currentPhoneRecord='';
        this.showDetaillist = !this.showDetaillist;

      },
      preDataSale(data) {
        if (data.length) {
          for (let i = data.length - 1; i >= 0; i--) {
            let e = data[i];
            let color = e.status + '';
            switch (color) {
              case '1':
                e.bgcolor = "#eeeeee";
                break;
              case '2':
                e.bgcolor = "yellowgreen";
                break;
              case '3':
                e.bgcolor = "green";
                break;
              case '4':
                e.bgcolor = "#e4e4e4";
                break;
              case '5':
                e.bgcolor = "#2974df";
                break;
              case '6':
                e.bgcolor = "#85ce61";
                break;
              default:
                break;
            }

          }
        }
        return data;
      },
      toTree(data) {
        let result = []
        if (!Array.isArray(data)) {
          return result
        }
        data.forEach(item => {
          delete item.child;
        });
        let map = {};
        data.forEach(item => {
          map[item.id] = item;
        });
        data.forEach(item => {
          let parent = map[item.parentClientId];
          if (parent) {
            (parent.child || (parent.child = [])).push(item);
          } else {
            result.push(item);
          }
        });
        console.log(result);
        return result;
      },
      lookSendDetail(item) {
        this.manageData=item;
        this.plansRecord = item.billList;
        console.log(item.billList);
        this.isRecord = true;
      },

      backMange() {
        this.isRecord = false;
        this.plansRecord = [];
        this.currentPhoneRecord='';
        this.manageData='';
      },

      //查看回访记录
      lookPhoneRecord(data){
        let _this = this;
        let  billId=data.id;
        this.currentPhoneRecord=data;
        this.tableLoading = true;
        this.getUrl = "/returnvisit/findByBillId/" +billId;
        this.getRequest(this.getUrl)
          .then( resp => {
              this.tableLoading = false;
              if (resp && resp.status === 200 && resp.data.success) {
                _this.detailsList = resp.data.data;
                console.log(_this.detailsList);
                if(_this.detailsList&&_this.detailsList.length){
                _this.showDetaillist =true;
                }else{
                    this.$message({
                      message:"未回访，请添加记录",
                      type:'warning'
                    })
                }
                _this.emptyData();
              }
            })
      },
      manageStatusFormat(row){
        console.log(row);
        var e=row.billstatus;
        return e===1?"未到":e===2?"到达":e===3?"已签收":"未知";
      },
      //编辑回访记录
      editeAsk(index,row){
       this.visit.note=row.note;
       this.visit.billId=row.billId;
       this.visit.billstatus=row.billstatus;
       this.visit.id=row.id;
       this.dialogVisitVisible=true;

      },

      //删除回访记录
      deleteAsk(index,row){
        this.$confirm('此操作将删除该条回访记录， 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doDeleteRecord(row.id);
        }).catch(() => {
        });
       },

      doDeleteRecord(ids) {
        this.tableLoading = true;
        var _this = this;
        this.getRequest("/returnvisit/delete?id=" + ids).then(resp => {
          _this.tableLoading = false;
          if (resp && resp.status === 200) {
            _this.loadData();
          }
        })
      },

    },
  }

</script>
<style scoped>

  .demo-table-expand label {
    width: 50px;
    color: #2974df;
  }

  .flex-send {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: start;
    width: 100%;
  }

  .flex-send .item-send {
    display: block;
    box-sizing: border-box;
    overflow: hidden;
    text-align: center;
    list-style: none;
    padding: 2px;
    position: relative;
    justify-content: stretch;

  }

  .in-send-show:hover {
    border-color: red;
    position: relative;
  }

  .in-send-show {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 1px solid #ccc;
    text-align: start;
    height: 140px;
    width: 520px;
    font-size: 12px;
    overflow-y: hidden;
  }

  .in-send-show .item-left {
    width: 140px;
    height: 140px;
    padding: 0 10px;
    background-color: #2fc5da;
  }

  .in-send-show .item-right {
    width: 420px;
    height: 140px;

  }

  .in-send-show .item-right .right-head {
    height: 100px;
  }

  .in-send-show .item-right .right-bottom {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    color: #2fc5da;
  }

  .right-bottom:hover {
    opacity: 0.6;
    cursor: pointer;
  }

  .send-input {
    width: 160px;
  }

  .send-head {
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center
  }

  .send-head-right-btn {
    margin-left: 5px;
    margin-right: 20px;
    display: inline
  }

  .send-head-left-btn {
    margin-left: 5px
  }

  .send-main {
    padding-left: 0px;
    padding-top: 0px

  }

  .send-main-div {
    margin-bottom: 10px;
    border: 1px;
    border-radius: 5px;
    border-style: solid;
    padding: 5px 0px 5px 0px;
    box-sizing: border-box;
    border-color: #20a0ff
  }

  /*——————————————————————————模块——————————————————————————————————————————————————*/
  .stamp {
    color: #2783ca;
  }

  .divce-mode {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
  }

  .product-model-base {
    width: 100%;
    height: auto;
    display: flex;
    padding: 0 20px 20px 20px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: start;
    box-sizing: border-box;
    background-color: #E5EFF1;
  }

  .product-model-item {
    box-sizing: border-box;
