      toTree(data) {
        console.log('toTree');
        let that = this;
        let result = []
        if (!Array.isArray(data)) {
          return result
        }
        var treeData = [];
        data.forEach(item => {
          treeData.push({
            'value': item.id,
            'label': item.name,
            'parentId': item.parentClientId
          })
        });
        let map = {};
        treeData.forEach(item => {
          map[item.value] = item;
        });
        treeData.forEach(item => {
          let parent = map[item.parentId];
          if (parent) {
            (parent.children || (parent.children = [])).push(item);
          } else {
            result.push(item);
          }
        });
        result = that.getResult(result)
        console.log(result);
        //将父parentId 删除掉
        return result;
      },
      getResult(reusult) {
        let that = this;
        reusult.map(item => {
          delete item.parentId;
          if (item.children && item.children.length) {
            that.getResult(item.children)
          }
        });
        return reusult;
      },
