            _this.lockList = data;
            _this.totalMaterialCount = resp.data.total;
            _this.dialoglockVisible = true;
            // _this.emptyData();
            // 清除选择的行
          }
        })
      },
      showLockRow(row) {
        console.log("click" + row);
        this.selectedlock = this.lockList.indexOf(row);
        this.selectLockObj = row;
        console.log(this.selectMaterial);
      },
      chooslocks() {
        console.log('选择原料了！');
        let vm = this;
        if (this.selectLockObj.id > 0) {
          const msg = this.selectLockObj.producteName;
          this.$confirm('您当前选择了' + msg + "确认选择该产品吗？", '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            vm.setlockData(vm);
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消'
            });
          });
        } else {
          this.$message({
            type: 'info',
            message: '请选择原材料！'
          })
        }

      },
      //设置备注
      noteChange(e){
        let that=this;
        if(e){
          that.salse.note=e;
        }
      },
      //当选择产品确认后的设置
      setlockData() {
        console.log('setlockData');
        let that = this;
        let v = that.selectLockObj;
        console.log(v);
        that.planDetail.productName = v.producteName;
        this.planDetail.productId=v.id;
        that.planDetail.proId = v.id; //选取的物料Id
        that.planDetail.specification = '';
        that.specslist = v.specs || [];
        if (that.specslist.length) {

          that.planDetail.specId = that.specslist[0].id;
          that.planDetail.specName=that.specslist[0].id;
          that.salse.specName=that.specslist[0].name;
        }

        that.colorlist = v.colors || [];
        if (that.colorlist.length) {
          that.planDetail.colorId = that.colorlist[0].id;
          that.planDetail.colorName=that.colorlist[0].id;
          that.salse.colorName=that.colorlist[0].name;
        }
        that.planDetail.productNo = v.proType;
        //设置销售计划中产品信息 产品名
        this.salse.productName=v.producteName;
        this.dialoglockVisible = false;
      },

      cancellock() {
        this.selectLockObj = {};
        this.selectedlock = {};
      },
      setSalesSpec(e){
        let that=this;
        console.log(e);
        this.filteName(this.specslist,e,'spec')

      },

      setSalesColor(e){
        console.log(e);
        this.filteName(this.colorlist,e,'color')
      },
      filteName(arr,id, type){
        let that=this;
        if(arr&&arr.length){
          for (let i = 0,n=arr.length; i <n ; i++) {
            if(id===arr[i].id){
              if(type==='spec'){
              that.salse.specName=arr[i].name;
              }else if(type==="color"){
               that.salse.colorName=arr[i].name
              }
            }
          }
        }
      },
      //下发数量
      plandetailCount(e){
        if(e){
          this.salse.expCount=e;
          this.setEndNumber(e)
        }
      }
    },


    computed: {
      routes() {
        return this.$store.state.routes
      },
        user() {
        return this.$store.state.user;
        },
    },

  }

</script>
