      // 改变数据
      handleClick(e, m, status) {
        let code;
        if (status && status !== '') {
          code = status;
          this.activePanel = status;
          this.tap = 2;
        } else {
          code = this.activePanel;
        }
        let plan = false;
        switch (code) {
          case "0":
            plan = true;
            break;
          case "1":
            this.manageStatus = 1;
            break;
          case "2":
            this.manageStatus = 2;
            break;
          case "3":
            this.manageStatus = 3;
            break;
          case "4":
            this.manageStatus = 4;
            break;
          case "5":
            this.manageStatus = '';
            break;
          default:
            break;
        }
        if (!plan) {
          this.mainProduct = true;
          this.loadData();
        } else {
          this.mainProduct = false;
          this.loadMaterial();
        }
      },
