      // 编号***********
      serialNumberAdd() {
        let sid = 0;
        if (this.serialNumbers.length) {
          let maxId = 0;
          //找出最大的Id+1
          this.serialNumbers.forEach(function (v, i, arr) {
            maxId = v.sid > maxId ? v.sid : maxId;
          })
          sid = maxId + 1;
        } else {
          sid = 1;
        }
        this.serialNumbers.push({
          sid: sid,
          id: 0,
          startNo: '',
          endNo: '',
          clientId: 0,
          productId: 0,
          type: "new"
        });
