      /**
       * 数据整理
       * @param Data
       * @returns {{length}}
       */
      preData(Data) {
        Data.forEach((plan) => {
          let aimQunity = plan.planDetails.actualQuantity;
          let nowQunity = plan.planDetails.accomplishNO;
          let nowRate = nowQunity / aimQunity;
          nowRate = (Math.round(nowRate * 10000) / 100.00);
          console.log(nowRate);
          plan.gres = nowRate;
          if (nowRate > 80 && nowRate < 100) {
            plan.color = 'gold'
          } else if (nowRate >= 100) {
            plan.mangeStatus = 3;
            plan.gres = 100;
          }
          let s = plan.mangeStatus;
          if (s !== null)
            switch (s) {
              case 1:
                plan.bgcolor = "bisque";
                break;
              case 2:
                plan.bgcolor = "lightyellow";
                break;
              case 3:
                plan.bgcolor = "palegoldenrod";
                break;
              case 4:
                plan.status = "exception";
                plan.bgcolor = "#eeeeee";
                break;
              default:
                break;
            }
          plan.onhover = false;

        })
        return Data;
      },
