                  _this.cancelSend();
                } else {
                  Message("添加失败")
                }
              })
            }
          } else {
            return false;
          }
        });


      },
      cancelSend() {
        this.confirmData = {
          id: 0,
          shippingRequestDetailsId: 0,//发货请求详情ID
          clientId: [],//货运部id
          dispatchBillNo: '',//单号
          boxs: '',//件数
          consignee: '',//收货人
          consigneePhone: '',//收货人电话
          address: '',//收货地址
          consigner: '',//发货人
          consignerPhone: '',//发货人电话
          remarks: '',//备注
          cargoName: '',//货名
          freights: "",//运费
          shortHaulMoney: "",//上门费
          freightDetails: "",//运费详情
          collFreight: '',//代收货款
          collFreightBig: '',//代收货款大写
          serviceCode: '',//服务方式
          receipt: '',//回单信息
          totalMoney: '',//代收货款大写
          // userName:'',//代收货款大写
          stype: '',//支付方式
          startDate: '',
          endDate: '',
        }
      },
      toTree(data) {
        let result = []
        if (!Array.isArray(data)) {
          return result
        }
        data.forEach(item => {
          delete item.child;
        });
        let map = {};
        data.forEach(item => {
          map[item.id] = item;
        });
        data.forEach(item => {
          let parent = map[item.parentClientId];
          if (parent) {
            (parent.child || (parent.child = [])).push(item);
          } else {
            result.push(item);
          }
        });
        console.log(result);
        return result;
      }
    },
    computed: {
      routes() {
        return this.$store.state.routes
      },

    },

  }

</script>
